/* settings icon */

.settings-icon {
  display: block;
  position: fixed;
  margin: 20px;
  width: 25px;
  /*background-color: white;*/
  padding: 3px;
  border-radius: 50%;
}
.settings-icon:hover {
  filter: drop-shadow(0 0 0.25rem white);
}

/* Clock In the Corner */

.corner-clock {
  margin: 20px -20px;
  padding: 3px;
  position: fixed;
  right: 50px;
  font-size: 22px;
  color: white;
}

/* Modal */

.modal {
  width: 100%;
  height: 100%;
  position: relative;
  background-color: gray;
  overflow: hidden;
}

.modal * {
  text-align: center;
}

/* Modal X button */

.modal-close {
  position: fixed;
  right: 5px;
  width: 40px;
  padding: 2px;
}

.modal-close:hover {
  filter: drop-shadow(0 0 0.25rem white);
}

/* Rich Context Stuff */

.richContextEditors {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.richContextContainer {
  /*padding: 5px;
  margin: 5px;
  background-color: white;
  border: 1px solid black;*/
  margin: 2px;
  display: flex;
}

.editorLabel {
  padding: 2px 2px;
  margin: 0;
}

.richContextEditor {
  height: 20px;
}

.footer {
  margin: 10px 10px;
}

.header {
  font-size: 40px;
}

/* Color Selector Inline */
.colorSelect {
  display: flex;
  align-items: center;
  justify-content: center;
}

.colorSelects {
  display: inline-flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.colorSelects > div {
  margin: -15px;
}

